/*============================
    Border Radius Utilities
============================*/

/*============================
   Pixel Values (0px  16px)
============================*/

/* rd0  border-radius: 0px */
.rd0,
.rd0x>* {
  border-radius: 0px !important;
}

.rd1,
.rd1x>* {
  border-radius: 1px !important;
}

.rd2,
.rd2x>* {
  border-radius: 2px !important;
