/*****************************
    Flex Containers Base
*****************************/
.fx {
  display: flex;
  box-sizing: border-box;
  overflow-wrap: break-word;
  width: 100%;
  flex-wrap: wrap;
}

/*****************************
    Flex Direction
*****************************/
.fx1,
.fx1x>* {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row !important;
}

.fx2,
.fx2x>* {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row-reverse !important;
}

.fx3,
.fx3x>* {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column !important;
}

.fx4,
.fx4x>* {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column-reverse !important;
}

/*****************************
    Flex Wrap
*****************************/
.fx5,
.fx5x>* {
  display: flex;
  flex-wrap: nowrap !important;
}

.fx6,
.fx6x>* {
  display: flex;
  flex-wrap: wrap !important;
}

.fx7,
.fx7x>* {
  display: flex;
  flex-wrap: wrap-reverse !important;
}

/*****************************
    Justify Content
*****************************/
.fx8,
.fx8x>* {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start !important;
}

.fx9,
.fx9x>* {
